import React, { Fragment } from "react";
import { NavLink } from "react-router-dom";

class ButtonMenu extends React.Component {
  constructor(props) {
    super(props)
    this.state = { selectedTab: null };
    this.menu = this.props.menu
    this.className = this.props.className
    this.onSelectTab = this.onSelectTab.bind(this);
    this.key = this.props.key
  }
  componentWillMount(){
    document.addEventListener('mousedown',this.handleClick, false);
  }
  componentWillUnmount(){
    document.removeEventListener('mousedown',this.handleClick, false);

  }
  handleClick = (e) =>{
    if(this.node.contains(e.target)){
      return;
    }    
    this.setState({ selectedTab: false });
  
  }
  

  onSelectTab(tabIndex) {
    // In case the user clicks again on the tab, unselect it
    const selectedTab = this.state.selectedTab === tabIndex ? false : tabIndex;
    this.setState({ selectedTab });
  }

  isSelected(index) {
    if (this.state.selectedTab === index) {
      return "open"
    } else {
      return "closed"
    }
  }

  renderLink(data, index) {
    if (data.url) {

      return (
        <NavLink to={data.url} key={data.Label}  activeClassName="selected">
          <button className={this.props.className}  >{data.Label} 
          
          </button>
        </NavLink>
      )
    } if (data.subnav) {
      return (
          <Fragment key={Math.random()}>
            <button className={this.props.className} key={data.Label}
              onClick={() => this.onSelectTab(index)}
              onBlur={() => this.onSelectTab(index)}
              onFocus={() => this.onSelectTab(index)}
              >
               
              {data.Label}
            </button>
            <li className={`submenu-${this.isSelected(index)}`} >
            {data.subnav.map((element,index) => (
            <NavLink to={element.url}  key={element.url} activeClassName="selected" >
            <button className="submenu-button">{element.Label} 
            </button>
          </NavLink>
            
            ))}
            </li>
          </Fragment >
          
          
          )
    } else {
      return (
        <button className={this.props.className} key={index}>Something went Wrong!
        </button>
      )
    }
  }

  render() {
    //inside my drawer
    return (
      <ul className="menu-list" ref={node =>this.node = node}>
        {this.menu.map(this.renderLink, this)}
      </ul>
    )
  }
}
export default ButtonMenu;
