import React ,{ useEffect,useState } from 'react';
import { AiOutlineFileExcel } from "react-icons/ai"
import "./styles.css"



const UsersImport = () => {
    const [selectedFile, setSelectedFile] = useState();
	const [IsSelected, setIsSelected] = useState(null);      

	const changeHandler = (event) => {
		setSelectedFile(event.target.files[0]);
        setIsSelected(true);
	};
    const deleteFile = () => {
		setSelectedFile(null);
		setIsSelected(false);
        console.log(IsSelected,selectedFile)
	};

	const handleSubmission = () => {
		const formData = new FormData();

		formData.append('File', selectedFile);

		fetch(
			'http://localhost:3001/api/upload',
			{
				method: 'POST',
				body: formData,
			}
		)
			.then((response) => response.json())
			.then((result) => {
				console.log('Success:', result);
			})
			.catch((error) => {
				console.error('Error:', error);
			});
	};
	const downloadExcelFile = () => {
		fetch('http://localhost:3001/api/excelFile',{
			method: 'GET',
        })
        .then(response => response.blob())
        .then(blob => {
            var url = window.URL.createObjectURL(blob);
            var a = document.createElement('a');
            a.href = url;
            a.download = "modelo.xlsx";
            document.body.appendChild(a); // we need to append the element to the dom -> otherwise it will not work in firefox
            a.click();    
            a.remove();  //afterwards we remove the element again         
        });
    }
    return (
    <div className="dashboardArea">
    <form className="file-upload">
        <div className="upload-wrap">
            <input className="file-upload-input" type='file' name="file" onChange={changeHandler}/>
            <div className="drag-text">
            {IsSelected ? (
				<div>
                    <AiOutlineFileExcel className="AiFillFileExcel"/>
					<p>Nome do Arquivo: {selectedFile.name}</p>
					<p>Tamanho em bytes: {selectedFile.size}</p>
					
				</div>
			) : (
				<h3>ARRASTE E SOLTE O ARQUIVO OU CLIQUE AQUI </h3>
			)}
            
            </div>
        </div>
    <button className="file-download-btn" type="button" onClick={downloadExcelFile} >Modelo Excel</button>
    </form>
    <div className="btn-upload">
    <button className="file-upload-btn" type="button" onClick={handleSubmission} >CONCLUIR ENVIO</button>
    <button className="remove-file" type="button" onClick={deleteFile} >CANCELAR ENVIO</button>
    
    </div>    

    </div>
      );
};

export default UsersImport;


