import { FaHome,FaUser,FaUsers,FaBahai,FaBible } from 'react-icons/fa';
import { HiMenu } from 'react-icons/hi';
import React from "react";
import SubMenu from './assets/SubMenu';
import "./styles.css";
  
class Sidebar extends React.Component {
  constructor(props){
    super(props)
    this.state = {open: false};
    this.className = this.props.className
    
  } 
  componentWillMount(){
    document.addEventListener('mousedown',this.handleClick, false);
  }
  componentWillUnmount(){
    document.removeEventListener('mousedown',this.handleClick, false);

  }
  handleClick = (e) =>{
    if(this.node.contains(e.target)){
      return;
    }    
    this.setState({ open: false });
  
  }
  handleDrawer(){
    let currentState = this.state.open
    this.setState({open: !currentState})

  }
  
  handleClickOut(){
      this.setState({open: false})
  }

  render () {
    let SideMenu = [{   
      Label:'Home',
      url:"/dashboard",
    },
    {
      Label:'Usuários',
      subnav:[
        {
        Label:'Cadastro',
        url:"/users/register",
        },
        {   
        Label:'Importar',
        url:"/users/import",
        },
        {   
        Label:'Pesquisar',
        url:"/users/search",
        },

      ]},
    ]


    return (
      <div className="Sidebar" ref={node =>this.node = node}>
      <HiMenu 
              onClick={this.handleDrawer.bind(this)}
              className="button-menu">
      </HiMenu>
      <ul className={`drawer-${ this.state.open ? "show" : "hide"}`}>
        <SubMenu className="menu-button"menu={SideMenu}></SubMenu>
      </ul>    
      
      </div>
    ) 
  }
}

export default Sidebar;