import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from 'axios';
import "./styles.css";
import { AiOutlineCluster } from "react-icons/ai";

const api = axios.create({
  baseURL: "http://127.0.0.1:3001"
});

export default class SignUp extends Component {
  constructor(props) {
    super(props)
    this.onChangeUserName = this.onChangeUserName.bind(this);
    this.onChangeUserEmail = this.onChangeUserEmail.bind(this);
    this.onChangeUserPassword = this.onChangeUserPassword.bind(this);
    this.onChangeUserConfirmPassword = this.onChangeUserConfirmPassword.bind(this);
    this.handleSignUp = this.handleSignUp.bind(this);
    this.state = {
      name: '',
      email: '',
      password: '',
      confirmPassword: '',
    }
  }
  onChangeUserName(e) {
    this.setState({ name: e.target.value })
  }

  onChangeUserEmail(e) {
    this.setState({ email: e.target.value })
  }
  onChangeUserPassword(e) {
    this.setState({ password: e.target.value })
  }

  onChangeUserConfirmPassword(e) {
    this.setState({ confirmPassword: e.target.value })
  }

  handleSignUp(e) {
    e.preventDefault()
    const userObject = {
      name: this.state.name,
      email: this.state.email,
      password: this.state.password,
      confirmPassword: this.state.confirmPassword
    };
    axios.post('http://localhost:3001/auth/signup', userObject)
      .then((res) => {
        alert(res.data.msg)
        window.location.reload();
      })
      .catch((err) => {
        let formatedMessage = err.response.data.error
        alert(formatedMessage.join('\r\n'))

      });
  }

  render() {
    return (
        <div className="login-box">
          <form className="form-signup" onSubmit={this.handleSignUp} >
          <AiOutlineCluster className='icon'/>
            <label className="login-title">Criar Usuario</label >
              <input type="text"
                className="signup"
                id="form"
                value={this.state.name}
                onChange={e => this.setState({ name: e.target.value })}
                required={true} placeholder="Email"
                autocomplete="off"
              />
              <input type="text"
                className="signup"
                id="form" value={this.state.email}
                onChange={e => this.setState({ email: e.target.value })}
                required={true} placeholder="Login"
                autocomplete="off"
              />
              <input type="password"
                className="signup"
                id="form"
                value={this.state.password}
                onChange={e => this.setState({ password: e.target.value })}
                required={true} placeholder="Password"
                autocomplete="off"
              />
              <input type="password"
                className="signup"
                id="form"
                value={this.state.confirmPassword}
                onChange={e => this.setState({ confirmPassword: e.target.value })}
                required={true} placeholder="Confirm Password"
                autocomplete="off"
              />
            <div className="form-submit-area">
              <button type="submit" className="login-Button">Criar Conta</button>
                  &nbsp;&nbsp;&nbsp;
                 <Link to="/login" className="login-Button">Página de Login</Link>
            </div>
          </form>
        </div>
    );
  }
}
