import React, { Fragment } from 'react';
import { AiOutlineIdcard, AiOutlineMonitor, AiOutlineUserAdd } from 'react-icons/ai';
import Card from '../../components/Card';
import "./styles.css"

class Dashboard extends React.Component {

  render() {

    return (
      <Fragment>
        <div className="main-content">
          <Card value="/users/register">
            <AiOutlineUserAdd className="card-icon" />
            <p className="card-text">Cadastrar Usuários</p>
          </Card>
          <Card value="/users/import">
            <AiOutlineIdcard className="card-icon" />
            <p className="card-text">Importar Usuários</p>
          </Card>
          <Card value="/users/search">
            <AiOutlineMonitor className="card-icon" />
            <p className="card-text">Buscar Usuários</p>
          </Card>
        </div>
      </Fragment>
    );
  };

}
export default Dashboard;
