import React from 'react';
import { Redirect } from 'react-router';
import "./styles.css";

const Err404 = () => {
  return (
    <div id="notfound">
    <div className="notfound">
        <div className="notfound-404">
            <h1>:(</h1>
        </div>
        <h2>404 - Page not found</h2>
        <p>A Página que você está procurando pode ter sido deletada, renomeada ou estar temporáriamente indisponível.</p>
        <button>home page<Redirect to="/dashboard" />
</button>
    </div>
</div>

  );
};

export default Err404;
