import React, { Fragment, useState } from "react";
import "./styles.css"

export default function Users(props) {
  const [state, setState] = useState({
    firstName: "",
    lastName: "",
    displayName: "",
    idt: "",
    email: "",
    initials: "",
  })

  function handleChange(e) {
    const value = e.target.value;
    setState({
      ...state,
      [e.target.name]: value
    });

  }

  function handleSubmit(e) {
    e.preventDefault();
    fetch('http://localhost:3001/api/users/register', {
      method: 'POST',
      body: JSON.stringify({ state }),
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
    }).then(response => {
      if (response.status >= 200 && response.status < 300) {
        return response;
      } else {
        console.log('Somtehing went wrong');
      }
    }).catch(err => err);
  }

  function checkParams(e) {
    e.preventDefault();
    alert(JSON.stringify(state))

  }

  function handleSearch(e) {
    e.preventDefault();
    alert(state)

  }

  return (
    <div className="dashboardArea">
      <form className="first-form" autocomplete="off">
        <input type="text"
          className="left-line-input"
          name="firstName"
          value={state.firstName}
          onChange={handleChange}
          placeholder="Primeiro Nome" />
        <input type="text"
          className="right-line-input"
          name="lastName"
          value={state.lastName}
          onChange={handleChange}
          placeholder="Sobrenome" />
        <input type="text"
          className="second-line-input"
          name="email"
          value={state.email}
          onChange={handleChange}
          placeholder="E-mail" />
        <input type="text"
          className="left-line-input"
          name="idt"
          value={state.idt}
          onChange={handleChange}
          placeholder="Identidade" />
        <input type="text"
          className="right-line-input"
          name="displayName"
          value={state.displayName}
          onChange={handleChange}
          placeholder="Nome de Exibição" />
        <div className="btn-area">
          <select name="initials"
            className="checkbox"
            value={state.initials}
            onChange={handleChange}>
            <option value="" selected disabled hidden>Setor: </option>
            <option value="1">Administração</option>
            <option value="2">RH</option>
            <option value="3">Gerente</option>
            <option value="4">Analista de sistemas</option>
            <option value="5">Operador</option>
          </select>
        </div>
        <div className="send-form-area">
          <button className="submit-form" onClick={checkParams}>Cadastrar</button>
        </div>
      </form>
    </div>
  );
}