import React from 'react';
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import Navbar from '../../components/Navbar';
import Sidebar from '../../components/Sidebar';
import "./styles.css"
import Dashboard from "../Dashboard";
import UsersRegister from "../UsersRegister";
import UsersImport from "../UsersImport";
import UsersSearch from "../UsersSearch";
import Err404 from "../Err404";

class Layout extends React.Component {

  render() {
    return (
      <BrowserRouter>
        <Route>
          <Navbar />
          <Sidebar />
        </Route>
        <Switch>
          <Redirect exact from="/" to="/dashboard" />
          <Route exact path="/dashboard" component={Dashboard} />
          <Route exact path="/users/register" component={UsersRegister} />
          <Route exact path="/users/import" component={UsersImport} />
          <Route exact path="/users/search" component={UsersSearch} />

          <Route component={Err404} />
        </Switch>
      </BrowserRouter>
    );
  };

}
export default Layout;
